const FULFILLED = '_FULFILLED';
const REJECTED = '_REJECTED';
const PENDING = '_PENDING';

export const fulfilled = (type) => type + FULFILLED;
export const rejected = (type) => type + REJECTED;
export const pending = (type) => type + PENDING;

